.estimate-form-container {
	background-color: #fff;
	height: 465px;
	padding: 1.875rem;
	position: fixed;
	right: 0px;
	top: 60px;
	width: 260px;
	z-index: 1000;
}
.form-title {
	align-items: center;
	display: flex;
	justify-content: center;
	margin-bottom: 2rem;
}
.form-title h3 {
	font-size: 20px;
	font-weight: 300;
}
.form-title i {
	color: #1c69d4;
	font-size: 20px;
	font-weight: 300;
	margin-right: 5px;
}
.privacy-checkbox {
	font-size: 12px;
	margin: 1rem 0;
}
.privacy-checkbox input {
	margin: 0 0.5rem 0 0;
}

.privacy-checkbox span {
	color: #666666;
	font-size: 12px;
	font-weight: bold;
}
.submit-button {
	color: #fff;
	display: flex;
	height: 43px;
	margin-right: 2.25rem;
	width: 100%;
}

.privacy-checkbox span {
	color: #666666;
	font-size: 12px;
	font-weight: bold;
}
.submit-button {
	color: #fff;
	display: flex;
	height: 43px;
	margin-right: 2.25rem;
	width: 100%;
}
@media screen and (max-width: 1228px) {
	.estimate-form-container {
		height: 385px;
	}
}

@media screen and (max-width: 768px) {
	.estimate-form-container {
		position: fixed;
		padding: 0.5rem;
		width: 98%;
		z-index: 1000;
		top: 50px;
		left: 0;
		right: 0;
		margin: auto;
	}
}

@media screen and (max-width: 425px) {
	.estimate-form-container {
		width: 97%;
	}
}

@media screen and (max-width: 375px) {
	.estimate-form-container {
		width: 96%;
	}
}

@media screen and (max-width: 320px) {
	.estimate-form-container {
		position: fixed;
		width: 95%;
	}
}
